@font-face {
  font-family: 'Wremena';
  src: url('./assets/fonts/wremena_regular.woff2') format('woff2'),
       url('./assets/fonts/wremena_regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

:root {
  --font: "Wremena", serif;
  --color-black: #000;
  --color-white: #fff;
  --color-green: #025e14;
  --color-orange: #ff4c14;
  --color-red: #cc0000;
  --color-blue: #0000b5;
  --color-purple: #8b1fff;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

html,
body,
#root {
  height: 100%;
}

body {
  margin: 0;
  background: var(--color-black);
  font-family: var(--font);
  font-weight: 300;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button {
  color: initial;
  font-family: initial;
  font-size: initial;
}

.modal-start {
  position: fixed;
  top: 1rem;
  right: 1rem;
  bottom: 1rem;
  left: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: var(--color-black);
  color: var(--color-white);
  font-size: 1.35rem;
  padding: 1rem 1.5rem;
  text-align: center;
  overflow: auto;

  &__text {
    text-shadow: 0 0 20px var(--color-red);
  }

  &__btn {
    appearance: none;
    font-size: 1.2rem;
    background: var(--color-blue);
    color: var(--color-white);
    border: 0;
    border-radius: 50%;
    width: 5rem;
    height: 5rem;
  }
}

.map {
  height: 100%;
  background: var(--color-black);
  z-index: 0;

  .leaflet-control-layers,
  .leaflet-popup-close-button,
  .leaflet-popup-tip-container {
    display: none;
  }

  .leaflet-popup-content-wrapper {
    background: rgba(255, 255, 255, 0.9);
  }

  .leaflet-popup-content {
    font-family: var(--font);
    font-size: 1rem;
    hyphens: auto;
  }

}

.error-gps {
  position: fixed;
  top: 1rem;
  right: 6rem;
  left: 1rem;
  background: var(--color-white);
  color: var(--color-blue);
  padding: 1rem;
  text-align: center;
  z-index: 1;

  &__text {
    margin: 0 0 .5rem;
  }

  &__btn {
    appearance: none;
    font-size: .9rem;
    background: var(--color-blue);
    color: var(--color-white);
    border: 0;
    padding: .35rem 1rem;
  }
}

.icon-gps {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 2.5rem;
  z-index: 1;
  pointer-events: none;
  filter: drop-shadow(0 0 10px var(--color-black));
}

.controls {
  position: fixed;
  right: 1rem;
  bottom: 1rem;
  z-index: 1;
  display: flex;
  flex-direction: column;

  button {
    font-size: 2rem;
    background: var(--color-white);
    border: 0;
    padding: 0;
    width: 3rem;
    height: 3rem;
    cursor: pointer;
    &:disabled {
      color: var(--color-black);
      background: var(--color-white);
      opacity: 0.7;
    }
  }
}

.info-toggle {
  position: fixed;
  top: 0;
  right: 0;
  border: 0;
  background: transparent;
  width: 5.5rem;
  padding: 1rem;
  z-index: 4;
  cursor: pointer;
  filter: drop-shadow(0 0 10px var(--color-black));
}

.info-overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(
    90deg,
    var(--color-blue) -5%,
    var(--color-black) 5%,
    var(--color-black) 95%,
    var(--color-blue) 105%
  );
  color: var(--color-white);
  font-size: 1.25rem;
  padding: 1rem;
  overflow: auto;
  z-index: 3;

  &__content {
    max-width: 46rem;
    font-size: clamp(1rem, 3vw, 1.5rem);
    margin: 5rem auto 8rem;

    @media (min-width: 900px) {
      margin-top: 1rem;
      margin-left: 6rem;
    }
  }

  &__title {
    margin-bottom: 2rem;
  }

  a {
    color: inherit;
  }

  p {
    margin: 0;
    & + h2 {
      margin-top: 1.5rem;
    }
  }

  h2 {
    margin: 0;
    font-weight: 400;
    font-size: inherit;
    text-transform: uppercase;
  }
}

.transition-screen {
  --gradient-color: var(--color-red);
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: radial-gradient(circle, var(--color-white) 0%, var(--gradient-color) 100%);
  z-index: 5;
  font-size: clamp(1.5rem, 3vw, 2.5rem);
  text-transform: uppercase;

  &.green {
    --gradient-color: var(--color-green);
  }

  &.orange {
    --gradient-color: var(--color-orange);
  }

  &.blue {
    --gradient-color: var(--color-blue);
  }

  &.purple {
    --gradient-color: var(--color-purple);
  }
}

.modal-marker {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: var(--color-black);
  padding: 1rem;
  text-transform: uppercase;
  cursor: pointer;
  z-index: 5;

  &__content {
    display: grid;
    grid-template-rows: auto 1fr;
    row-gap: 1rem;
    height: calc(100% - 2rem);
  }

  &__title {
    margin: 0 auto;
    font-size: clamp(2rem, 4vw, 4rem);
    color: var(--color-white);
    font-weight: 300;
    text-align: center;
    max-width: 20ch;
  }

  &__image-container {
    position: relative;
  }

  &__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

.desktop-subtitle {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  color: var(--color-white);
  font-size: 2rem;
  text-align: center;
  padding: 1rem;
  text-shadow: 0 0 20px var(--color-red);
  z-index: 2;
}

.deskop-transition {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--color-black);
  z-index: 1;

  svg {
    width: 70%;
    height: 70%;
  }
}

.slideshow {
  color: var(--color-white);

  &__slide {
    position: relative;
    height: 100vh;

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
}
